
import { useRoute, useRouter } from "vue-router"
export default {
  name: "NavBar",
  props: {
    title: String,
    leftShow: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const Route = useRoute()
    const Router = useRouter()
    const back = () => {
      Router.back()
    }
    return {
      back,
    }
  },
}
